.custom-navbar {
  position: absolute;
  top: -47px;
  right: 15px;
  background: #ffb606;
  padding: 7px 10px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
  -webkit-transition: .2s;
  -moz-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  cursor: pointer;
  display: none
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .custom-navbar {
      display: block
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .custom-navbar {
      display: block
  }
}

@media (max-width: 575.98px) {
  .custom-navbar {
      display: block
  }
}

